<template>
	<div class="check_page1">
		<div class="cont box-border-shadow">
			<el-row :gutter="20">
			  <el-col :span="24">
			  	<div class="grid-content">
			  		<div style="margin: 15px auto;margin-bottom:0;width: 50%">
					  <el-input placeholder="请输入内容" v-model="input" class="input-with-select" :select="selectItem()">
					  	<el-select style="width: 130px" v-model="select" slot="prepend" placeholder="请选择">
					      <el-option label="科目名称" value="科目名称"></el-option>
					      <el-option label="项目名称" value="项目名称"></el-option>
					      <el-option label="负责人姓名" value="负责人姓名"></el-option>
					      <el-option label="管理部门" value="管理部门"></el-option>
					    </el-select>
					    <el-button slot="append" icon="el-icon-search" @click="getGraphEchart(input)"></el-button>
					  </el-input>
					</div>
					<div class="dec">
						<ul>
							<li>推荐：</li>
							<li class="item" v-for="item in tuijianList" :title="item" @click="getGraphEchart(item)">{{item}};</li>
						</ul>
					</div>
					<div style="margin:10px auto;text-align: center;">
					    <el-radio v-model="radio" label="1" border>知识图谱</el-radio>
					    <el-radio v-model="radio" label="2" border>核心节点挖掘</el-radio>
					    <el-radio v-model="radio" label="3" border>最短路径</el-radio>
					</div>
			  		<div v-show="radio=='1'" class="EChartsBox">
						<div v-show="!isData">
							<img style="display:block;margin: 50px auto;" src="../../../assets/no-sj.png">
						</div>
			  			<div v-show="isData" style="position: relative;">
						<div id="graphCharts" :style="{width:windowWidth+'px'}" style="height: 800px;"></div>
						</div>
			  		</div>
			  		<div v-show="radio=='2'" class="listBox">
			  			<table cellpadding="0" cellspacing="0" class="table_list">
			  				<thead>
			  					<tr>
			  						<th>节点1</th>
			  						<th>节点2</th>
			  						<th>关键节点</th>
			  					</tr>
			  				</thead>
			  				<tbody>
			  					<tr v-for="items in listBoxData">
			  						<td>{{items['p1.name']}}</td>
			  						<td>{{items['p2.name']}}</td>
			  						<td>{{items.PivotalNode}}</td>
			  					</tr>
			  				</tbody>
			  			</table>
			  		</div>
					<div v-show="radio=='3'" class="listBox">
						<!-- <div style="margin: 15px auto;margin-bottom:0;width: 50%">
						  <el-input placeholder="请输入内容" v-model="input2" class="input-with-select" :select="selectItem()">
						  	<el-select style="width: 130px" v-model="select2" slot="prepend" placeholder="请选择">
						      <el-option label="科目名称" value="科目名称"></el-option>
						      <el-option label="项目名称" value="项目名称"></el-option>
						      <el-option label="负责人姓名" value="负责人姓名"></el-option>
						      <el-option label="管理部门" value="管理部门"></el-option>
						    </el-select>
						    <el-button slot="append" icon="el-icon-search" @click="getGraphEchart(input2)"></el-button>
						  </el-input>
						</div>
						<div class="dec">
							<ul>
								<li>推荐：</li>
								<li class="item" v-for="item in tuijianList" :title="item" @click="getGraphEchart(item)">{{item}};</li>
							</ul>
						</div> -->
			  			<table cellpadding="0" cellspacing="0" class="table_list">
			  				<thead>
			  					<tr>
			  						<th>起始节点</th>
			  						<th>路径节点</th>
			  						<th>终止节点</th>
			  					</tr>
			  				</thead>
			  				<tbody>
			  					<tr v-for="items in listBoxData2">
			  						<td>{{items['起始节点']}}</td>
			  						<td>{{items['路径节点']}}</td>
			  						<td>{{items['终止节点']}}</td>
			  					</tr>
			  				</tbody>
			  			</table>
			  		</div>
			  	</div>
			  </el-col>
			</el-row>
		</div>
	</div>
</template>
<script type="text/javascript">
	import Echarts from '../Echarts'
    import * as echarts from 'echarts'
	import reqData from "./../../../reqData.js"
	export default{
		name:'check_page1',
		data(){
			return{
				radio: '1',
				windowWidth:(window.innerWidth)*0.85,
				input:'',
				select: '科目名称',
				tuijianListAll:{
					'科目名称':['后勤保障支出／后勤支出／商品和服务支出／电费','存货／教学材料','后勤保障支出／后勤支出／工资福利支出／基本工资／岗位工资'],
					// '科目名称':['电费','教学材料','岗位工资','奖励性绩效工资','课时费','外聘教师支出','养老保险','在职人员'],
					'项目名称':['陈维涛-科研启动费','大学科技园筹建办公室-水电费','会计学院-部门成人培训结算专项','科研部-科研项目评审与鉴定经费'],
					'负责人姓名':['徐振龙','陈丹萍','蔡雪辉','孙文远','赵慧敏','姚德文','王军','胡亚梅','刘顺','胡宁生','马小宁'],
					'管理部门':['财务部','会计学院','总务委员会','国际审计学院','管理科学与工程学院','法学院','工商管理学院','金融学院','文学院']
				},
				tuijianList:[],
				isData:true,
				listBoxData:[],
				listBoxData2:[]
			}
		},
		components:{
			Echarts
		},
		computed:{
			userName(){
				if(sessionStorage.getItem('username')){
					return sessionStorage.getItem('username');
				}
			},
		},
		methods: {
			selectItem(){
				console.log(this.select);
				this.tuijianList=this.tuijianListAll[this.select];
			},
			unique(arr,links){
				var newArr = [arr[0].name];
		   		var data=[arr[0]];
		   		//去重复项
		  　　 	for(var i=1;i<arr.length;i++){
		　　　　		if(newArr.indexOf(arr[i].name) == -1){
		          　　  newArr.push(arr[i].name);
		          	    data.push(arr[i]);
		    　　  	}
	    		}
	    		//区分值和主题
	    		for(var i=0;i<data.length;i++){
	    		 	if(data[i].tag&&data[i].tag==='值'){
	    		 		data[i].itemStyle={
				             normal: {
				                 color: '#68bdf6',
				                 borderColor:'#5ca8db',
				                 borderWidth:2,
				             }
				         }
				         data[i].symbol='diamond'
	    		 	}
	    		}
	    		console.log(data);
		        return data;
		  },
		  getGraphEchart(word){
		  	this.isData=true;
		 	 var graphCharts = echarts.init(document.getElementById('graphCharts'));
		 	 graphCharts.showLoading();
		 	 reqData
		        .req({
		          apiName: "NSDataNeo4jToJson",
		           data: {             
		             word:word,
		             word1:'被审单位会计科目'
		           }
		        })
		        .then(res => {
		          res = res.data;
		          if (res && res.code === "200") {
		            let data = res.data;
		            console.log(data);
		            let nodes=data.nodes
					let links=data.links;
					if(nodes.length>0){
				    	console.log(nodes);
						nodes=this.unique(nodes);
						// links=this.linksFormat(links);
						graphCharts.hideLoading();
				    	graphCharts.setOption(this.graphOption(nodes,links)); 
			    	    $(window).resize(function(){
							graphCharts.resize({
								width:(window.innerWidth)*0.85,
								height:'auto'
							})
					    })
					}else{
						this.isData=false;
					}
		          }
		        })
		        .catch(err => {
		          console.log(err);
		       });
		        reqData
		        .req({
		          apiName: "PivotalNodesToJson",
		           data: {             
		             word:word,
		             word1:'被审单位会计科目'
		           }
		        })
		        .then(res => {
		          res = res.data;
		          if (res && res.code === "200") {
		            let data = res.data;
		            console.log(data);
		            this.listBoxData=data.nodes
		          }
		        })
		        .catch(err => {
		          console.log(err);
		       });
		  },
		  graphOption(nodes,links){
		    	let option = {
				     title: {
				         // text: '知识图谱'
				     },
				     tooltip: {
				         formatter: function(x) {
				             return x.data.name;
				         }
				     },
				     series: [{
				         type: 'graph',
				         layout: 'force',
				         focusNodeAdjacency: true,
				         symbolSize: 68,
				         roam: true,
				         edgeSymbol: ['', 'arrow'],
				         // edgeSymbolSize: [80, 10],
				         hoverAnimation:true,
				         force: {
				         	initLayout:'circular',
				             repulsion: 3620,
            				 layoutAnimation:false,
            				 gravity:0.5,
				             edgeLength: [86, 180],
				             layoutAnimation:true
				         },
				         draggable: true,
						 // symbol:'diamond',
				         itemStyle: {
				             normal: {
				                 color: '#ff756e',
				                 borderColor:'#e06760',
				                 borderWidth:2,
				             },

				         },
				         lineStyle: {
				             normal: {
				                 width: 1,
				                 color: '#000'

				             }
				         },
				         edgeLabel: {
				             normal: {
				                 show: true,
				                 textStyle: {fontSize: 14,color:'#000'},
				                 formatter: function(x) {
				                 	let name=x.data.name;
						         	if(name.length>5){
										name=name.substr(0,5)+'...';
										// v.nameSub=name;
									}else{
										name=name.substr(0,5);
										// v.nameSub=name;
									}
				                     return name;
				                 }
				             }
				         },
				         label: {
				         	show:true,
				            formatter: function(x) {
			                 	let name=x.data.name;
					         	if(name.length>3){
									name=name.substr(0,3)+'...';
									// v.nameSub=name;
								}else{
									name=name.substr(0,3);
									// v.nameSub=name;
								}
			                     return '{a|'+name+'}';
			                },
			                rich: {
						        a: {
						            color: '#fff',
						        },
						    }
				         },
				        nodes: nodes, 
						links:links 
				    }]
				};
				return option;
		  }
	    },
	    mounted(){
	    	
	    }

	}
</script>
<style type="text/css" scoped="">
	.check_page1 .cont{
		width: 90%;
	    margin: 30px auto;
	    min-height: 600px;
	    /*padding: 40px;*/
	    box-sizing: border-box;
	} 
	.check_page1 .el-select .el-input {
	  width: 130px;
	}
	.check_page1 .dec{
		width: 50%;
		margin: 0 auto;
		height: 44px;
		white-space: nowrap;
	    overflow: hidden;
	    text-overflow: ellipsis;
	}
	.check_page1 .dec li{
		float: left;
		margin: 10px 0;
		color: #999;
	}
	.check_page1 .dec li.item{
		margin-right: 10px;
		cursor: pointer;
	}
	.check_page1 .dec li.item:hover{
		color: #b80000;
	}
	.check_page1 .cont .table_list{
		width: 80%;
		margin: 0 auto 20px;
	}
	.check_page1 .cont .table_list td,
	.check_page1 .cont .table_list th{
		border: 1px solid #ccc;
		text-align: center;
		line-height: 40px;
	}
	.check_page1 .cont .table_list th{
		border: none;
		background-color: rgb(184,0,0,0.7);
		color: #fff;
		line-height: 44px;
	}
</style>


